.hero-section {
	position: relative;
	width: 100%;
	padding-top: 20px;

	@media (min-width: $lg) {
		padding-top: 154px; 
	}

	&-inner {
		display: flex;
		flex-wrap: wrap;
	}

	&-box {
		max-width: 480px;
		margin: 0 auto;

		@media (min-width: $sm) { 
			max-width: 601px;
		}

		@media (min-width: $md) { 
			margin: 100px auto ;
		}

		@media (min-width: $xl) { 
			margin: 65px 0 120px;
		}
	}

	&-heading {
		font-family: $manrope;
		font-weight: $fw400;
		text-align: center;
		font-size: $fs32px;
		line-height: $lh36px;
		color: $black;
		padding-bottom: 16px;

		@media (min-width: $sm) { 
			font-size: $fs48px;
			line-height: $lh72px;
		}

		@media (min-width: $xl) { 
			text-align: left;
		}
	}

	&-text {
		font-family: $openSans;
		font-weight: $fw400;
		text-align: center;
		font-size: $fs16px;
		line-height: 28px;
		color: $grey;
		margin-bottom: 55px;

		@media (min-width: $xl) { 
			text-align: left;
		}
	}

	&-buttons {
		display: flex;
		justify-content: center;

		@media (min-width: $xl) { 
			justify-content: flex-start;
		}
	}

	&::before {
		content: "";
		display: inline-block;
		height: 100vw;
		width: 100vw;
		border-radius: 50%;
		background-color: #F1F1F1;
		position: absolute;
		top: 0;
		right: 0;
		transform: translate(56%, -62%);
	}

	&-media {
		justify-content: center;
		padding-top: 40px;

		@media (min-width: $sm) { 
			margin: 0 auto;
		}

		@media (min-width: $lg) { 
			width: calc(100% - 601px);
			padding-left: 20px;
		}

		@media (min-width: $xl) {
			padding: 0; 
			padding-left: 105px;
		}

		& img {
			width: 100%;
			max-height: 423px;
		}
	}
}

.brands {
	padding: 40px 0 20px;
	display: flex;
	flex-wrap: wrap;
	margin: 0 -28px;

	@media (min-width: $lg) { 
		padding: 60px 0 40px;
	}

	@media (min-width: $xl) {
		padding: 0; 
		margin-bottom: 90px;
	}

	&-logo {
		width: calc(100% / 3);
		text-align: center;
		height: 42px;
		padding: 0 28px;

		@media (min-width: $lg) { 
			width: calc(100% / 6);
		}
	}

	&-logo img {
		max-width: 100%;
	}
}